// /*
// _____Variables_____________________________________________________________*/
// ==========================================================================*/
// $tile-width: 4.25em;
$tile-width: 4.25em;
// $tile-height: 6.5em;
$tile-ratio: 6.5 / 4.5;
$tile-height: $tile-width * $tile-ratio;
// $tile-depth: .5em;
$tile-depth: $tile-width * .15;

html {
    position: relative;
    scrollbar-color: #2b353f #111417;
    font-size: 16px;

    // Font-size based on viewport width
    @media (max-width: 1920px) {
        font-size: 16px;
    }

    @media (max-width: 1280px) {
        font-size: 15px;
    }

    @media (max-width: 1180px) {
        font-size: 14px;
    }

    @media (max-width: 1080px) {
        font-size: 13px;
    }

    @media (max-width: 980px) {
        font-size: 12px;
    }

    @media (max-width: 880px) {
        font-size: 11px;
    }

    @media (max-width: 793px) {
        font-size: 10px;
    }

    
    // Font-size based on viewport height
    @media (max-height: 900px) {
        font-size: 15px;
    }

    @media (max-height: 850px) {
        font-size: 14px;
    }

    @media (max-height: 790px) {
        font-size: 13px;
    }

    @media (max-height: 675px) {
        font-size: 11px;
    }

    @media (max-height: 625px) {
        font-size: 10px;
    }
}

body {
    position: relative;
    background-color: rgb(0, 0, 0);
    // background-image: url("./../images/bg-1024.jpg");
    background-attachment: fixed;
    background-size: cover;
    background-position: center;
    color: aqua;

    // @media (min-width: 1025px) {
    //     background-image: url("./../images/bg-2048.jpg");
    // }

    // @media (min-height: 1025px) {
    //     background-image: url("./../images/bg-2048.jpg");
    // }

    // @media (min-width: 2049px) {
    //     background-image: url("./../images/bg-4096.jpg");
    // }

    // @media (min-height: 2049px) {
    //     background-image: url("./../images/bg-4096.jpg");
    // }
}

::selection {
    background-color: rgb(0, 5, 7);
    color: rgb(0, 183, 255);
}

a {
    color: rgb(0, 162, 255);

    &:hover {
        color: rgb(149, 216, 255);
    }

    &:active {
        color: rgb(0, 115, 182);
    }
}

fieldset {
    border-color: aqua;
    border-radius: 1rem;
    padding: 1rem;
}

input[type=radio],
input[type=checkbox] {
    margin-bottom: 1rem;
    margin-right: .5rem;
}

hr {
    border: none;
    border-top: solid 1px rgba(0, 255, 255, 0.15);
}

.button {
    border: solid 2px rgba(0, 255, 255, 0.5);
    background-color: rgba(0, 255, 255, 0.05);
    color: aqua;
    border-radius: 2rem;
    padding: .5rem 1rem;
    font-size: 1.25rem;
    cursor: pointer;

    &:hover {
        background-color: rgba(0, 255, 255, 0.25);
    }
}

.board-viewport {
    position: relative;
    top: 0;
    left: 0;
    width: 100%;
    min-height: 100vh;
    // overflow: auto;
    pointer-events: all;
    z-index: 1;
    
    @media(max-width: 768px) {
        overflow: auto;
    }
}

.board-wrapper {
    position: relative;
    top: 0;
    left: 0;
    width: 17 * $tile-width;
    height: 9 * $tile-height;
    display: flex;
    justify-content: center;
    align-items: center;
    margin: auto;
    pointer-events: none;
    padding-bottom: $tile-height / 2;

    @media (min-height: 1079px) {
        padding-top: $tile-height / 2;
    }

    @media (max-width: 480px) {
        padding-top: $tile-height / 2;
    }
}

.background {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh;
    pointer-events: none;
    background-attachment: fixed;
    background-size: cover;
    background-position: center;
}

.bg-low {
    background-image: url("./../images/bg-1024.jpg");
}

.bg-medium {
    background-image: url("./../images/bg-2048.jpg");
}

.bg-high {
    background-image: url("./../images/bg-4096.jpg");
}


.board {
    position: relative;
    width: 14 * $tile-width;
    height: 8 * $tile-height;
    pointer-events: none;
}

.layer {
    position: absolute;
    top: 0;
    left: 0;
    pointer-events: none;
    width: 14 * $tile-width;
}

.layer2 {
    pointer-events: none;
    top: -$tile-depth;
    left: -$tile-depth;
}

.layer3 {
    pointer-events: none;
    top: -2 * $tile-depth;
    left: -2 * $tile-depth;
}

.layer4 {
    pointer-events: none;
    top: -3 * $tile-depth;
    left: -3 * $tile-depth;
}

.row {
    pointer-events: none;
    clear: both;
    width: 14 * $tile-width;
}

.tile {
    position: relative;
    width: $tile-width;
    height: $tile-height;
    // border: solid black 1px;
    border-right: solid rgba(255, 255, 255, 0.4) 2px;
    border-bottom: solid rgba(255, 255, 255, 0.25) 2px;
    float: left;
    display: flex;
    align-items: center;
    justify-content: center;
    box-sizing: border-box;
    background-color: grey;
    pointer-events: all;
    cursor: pointer;
    background-size: 900% 500%;
    // background-image: url("./../images/tilesheet-ultra.png");


    &:before {
        position: absolute;
        content: "";
        width: $tile-depth;
        height: $tile-height;
        background-color: rgb(102, 102, 102);
        background-size: 900% 500%;
        // background-image: url("./../images/tilesheet-ultra.png");
        background-position: -700% -300%;
        top: $tile-depth / 2;
        left: $tile-width - .05em;
        transform: skewY(45deg);
        box-sizing: border-box;
    }

    &:after {
        position: absolute;
        content: "";
        width: $tile-width;
        height: $tile-depth;
        background-color: rgb(51, 51, 51);
        background-size: 900% 500%;
        // background-image: url("./../images/tilesheet-ultra.png");
        background-position: -800% -300%;
        border-right: rgba(255, 255, 255, 0.5) solid 1px;
        border-bottom: solid rgba(0, 0, 0, 0.35) 2px;
        top: $tile-height - .05em;
        left: $tile-depth / 2;
        transform: skewX(45deg);
        box-sizing: border-box;
    }


    &-low {
        background-image: url("./../images/tilesheet-low.png");

        &:before {
            background-image: url("./../images/tilesheet-low.png");
        }

        &:after {
            background-image: url("./../images/tilesheet-low.png");
        }
    }

    &-medium {
        background-image: url("./../images/tilesheet-medium.png");

        &:before {
            background-image: url("./../images/tilesheet-medium.png");
        }

        &:after {
            background-image: url("./../images/tilesheet-medium.png");
        }
    }

    &-high {
        background-image: url("./../images/tilesheet-high.png");

        &:before {
            background-image: url("./../images/tilesheet-high.png");
        }

        &:after {
            background-image: url("./../images/tilesheet-high.png");
        }
    }

    &-veryhigh {
        background-image: url("./../images/tilesheet-veryhigh.png");

        &:before {
            background-image: url("./../images/tilesheet-veryhigh.png");
        }

        &:after {
            background-image: url("./../images/tilesheet-veryhigh.png");
        }
    }

    &-ultra {
        background-image: url("./../images/tilesheet-ultra.png");

        &:before {
            background-image: url("./../images/tilesheet-ultra.png");
        }

        &:after {
            background-image: url("./../images/tilesheet-ultra.png");
        }
    }
}

.special-position-left {
    position: absolute;
    top: 3.5 * $tile-height;
}

.special-position-right1 {
    position: absolute;
    top: 3.5 * $tile-height;
    left: 13 * $tile-width;
}

.special-position-right2 {
    position: absolute;
    top: 3.5 * $tile-height;
    left: 14 * $tile-width;
}

.special-position-top {
    position: absolute;
    // top: 22.5em;
    top: 3.5 * $tile-height - 4 * $tile-depth;
    // left: 30.5em;
    left: 6.5 * $tile-width - 4 * $tile-depth;
}

.tile-selected {
    box-shadow: inset greenyellow 0 0 2em;
}

.tile-hint {
    // box-shadow: inset rgb(0, 255, 255) 0 0 3em;
    animation-name: hintpulse;
    animation-duration: .5s;
    animation-direction: alternate;
    animation-iteration-count: infinite;
    animation-timing-function: linear;
}

@keyframes hintpulse {
    0% {box-shadow: inset rgb(0, 255, 255) 0 0 .5em;}
    50% {box-shadow: inset rgb(255, 124, 244) 0 0 1.25em;}
    100% {box-shadow: inset rgb(255, 216, 171) 0 0 2em;}
}


.tile-dim {
    box-shadow: inset rgba(0, 0, 0, .9) 0 0 6em;
    border-right: solid rgba(0, 0, 0, 0.4) 2px;
    border-bottom: solid rgba(0, 0, 0, 0.25) 2px;

    &:before,
    &:after {
        box-shadow: inset rgba(0, 0, 0, .9) 0 0 6em;
        border-right: rgba(0, 0, 0, 0.5) solid 1px;
        border-bottom: solid rgba(0, 0, 0, 0.35) 2px;

    }
}

.tile-inactive {
    box-shadow: inset rgba(0, 0, 0, 0.9) 0 0 10em;
    cursor: auto;
    border-right: solid rgba(0, 0, 0, 0.4) 2px;
    border-bottom: solid rgba(0, 0, 0, 0.25) 2px;

    &:before,
    &:after {
        box-shadow: inset rgba(0, 0, 0, .9) 0 0 10em;
        border-right: rgba(0, 0, 0, 0.5) solid 1px;
        border-bottom: solid rgba(0, 0, 0, 0.35) 2px;

    }
}

.empty {
    width: $tile-width;
    height: $tile-height;
    // border: solid white 1px;
    float: left;
    display: flex;
    align-items: center;
    justify-content: center;
    // color: white;
    box-sizing: border-box;
    pointer-events: none;
}


// /*
// _____Modals________________________________________________________________*/
// ==========================================================================*/
.modal-bg {
    position: fixed;
    width: 100vw;
    height: 100vh;
    top: 0;right: 0;bottom: 0;left: 0;
    background-color: rgba(0, 0, 0, 0.8);
    overflow: auto;
    z-index: 3;
}

$modal-frame: #2a2e2e;
$modal-highlight: #7c8a8a;
$modal-shadow: #111313;
$modal-bg: #111417;

.modal-container {
    // position: relative;
    background-color: #2a2e2e;
    // background-image: linear-gradient(90deg, rgba(42,46,46,1) 0%, rgba(85,96,96,1) 50%, rgba(42,46,46,1) 100%);
    background-image: linear-gradient(90deg, $modal-frame 0%, lighten($modal-frame, 20%) 50%, $modal-frame 100%);
    border-radius: 2.25rem;
    padding: 1rem;
    border-top: 2px solid $modal-highlight;
    border-bottom: 2px solid $modal-shadow;
    box-sizing: border-box;

    @media (max-width: 480px) {
        padding: .5rem;
    }
}

.modal-content {
    position: relative;
    background-color: #111417;
    // background-image: radial-gradient(circle, rgb(31, 37, 44) 0%, rgba(17,20,23,1) 100%);;
    background-image: radial-gradient(circle, lighten($modal-bg, 8%) 0%, $modal-bg 100%);;
    height: 100%;
    width: 100%;
    border-radius: 1.5rem;
    padding: 2rem;
    border-bottom: 2px solid $modal-highlight;
    border-top: 2px solid $modal-shadow;
    box-shadow: inset 0 0 3rem darken($modal-bg, 5%);
    box-sizing: border-box;

    font-size: 16px;


    @media (max-width: 480px) {
        padding: 1rem;
    }
}

.modal-close-button {
    position: absolute;
    top: 1rem;
    right: 1rem;
    width: 2.25rem;
    height: 2.25rem;
    padding: .25rem;
    box-sizing: border-box;
    overflow: hidden;

    svg {
        position: relative;
        width: 100%;
        height: 100%;
        box-sizing: border-box;
        fill: aqua;
        pointer-events: none;
    }
}




.modal-about,
.modal-settings {
    width: 640px;
    margin: 5rem auto;

    @media (max-width: 640px) {
        width: 100%;
        margin: 0;
        border-radius: 0;
    }
}

.modal-settings {
    
}


// /*
// _____Status Bar____________________________________________________________*/
// ==========================================================================*/
.status-bar {
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    // background: rgba(0, 0, 0, 0.75);
    // background-image: url("./../images/hud.png");
    background-size: contain;
    background-position: bottom center;
    background-repeat: no-repeat;
