    // visibility: hidden;
    color: aqua;
    box-sizing: border-box;
    pointer-events: none;

    // @media(max-width: 768px) {
    //     display: none;
    // }
}

.hud-settings {
    position: absolute;
    bottom: 0;
    left: 11rem;
    width: 10.5rem;
    height: 4.5rem;
    border-top-right-radius: 2rem;
    border-top-left-radius: 0;
    border-bottom-left-radius: 0;
    border-bottom-right-radius: 0;
    padding: .5rem;
    // background-image: url("./../images/hud-settings.png");
    // background-size: contain;
    // background-position: bottom left;
    // background-repeat: no-repeat;
    border-left: solid 1px #000;

    .modal-content {
        padding: .15rem;
    }
    
    @media (max-width: 793px) {
        width: 11rem;
    }
    
    @media (max-height: 793px) {
        width: 11rem;
    }

    @media (max-width: 768px) {
        border-left: none;
        left: unset;
        right: 0;
        height: 12rem;
        width: 4.25rem;
        border-top-left-radius: 2rem;
        border-top-right-radius: 0;

        #button-settings,
        #button-about,
        #button-donate {
            position: absolute;
            margin-left: 0;
            left: .3rem
        }

        #button-about {
            top: 3.7rem;
        }

        #button-settings {
            top: 7rem;
        }
    }
}

.hud-timer {
    position: absolute;
    bottom: 0;
    left: 50%;
    width: 17rem;
    height: 4rem;
    transform: translate(-50%, 0);
    border-top-right-radius: 2rem;
    border-top-left-radius: 2rem;
    border-bottom-left-radius: 0;
    border-bottom-right-radius: 0;
    padding: .5rem .75rem;
    // background-image: url("./../images/hud-timer.png");
    // background-size: contain;
    // background-position: bottom center;
    // background-repeat: no-repeat;

    .modal-content {
        padding: .15rem;
    }

    @media (max-width: 480px) {
        bottom: unset;
        top: 0;
        border-top-left-radius: 0;
        border-top-right-radius: 0;
        border-bottom-left-radius: 2rem;
        border-bottom-right-radius: 2rem;
    }
}

.hud-interactions {
